import {
  FEATURES,
  DATA_POINT_KEYS,
  deleteRedundantAttackProps,
  attackKey,
} from "./utils"
import { ResponsiveRadar } from "@nivo/radar"

const radarResultsAccuracy = (v) => {
  try {
    const inverseDistance = 0.2 / Math.abs(0.5 - v)
    return -1 + 2 / (1 + Math.exp(-inverseDistance))
  } catch {
    return 0
  }
}

const dataMappers = {
  "Train Time": (x) => -1 + 2 / (1 + Math.exp(-1 / x)),
  "MPTL Accuracy (All)": radarResultsAccuracy,
  "MPTL Accuracy (OUT)": radarResultsAccuracy,
  "MPTL Accuracy (IN)": radarResultsAccuracy,
  "MPTL Far Weighted": radarResultsAccuracy,
  "ART Blackbox Accuracy (All)": radarResultsAccuracy,
  "ART Blackbox Accuracy (OUT)": radarResultsAccuracy,
  "ART Blackbox Accuracy (IN)": radarResultsAccuracy,
  "MI Accuracy (All)": radarResultsAccuracy,
  "ART Rule Based Accuracy (All)": radarResultsAccuracy,
  "ART Rule Based Accuracy (OUT)": radarResultsAccuracy,
  "ART Rule Based Accuracy (IN)": radarResultsAccuracy,
  "ART Label-Only DB Accuracy (All)": radarResultsAccuracy,
  "ART Label-Only DB Accuracy (OUT)": radarResultsAccuracy,
  "ART Label-Only DB Accuracy (IN)": radarResultsAccuracy,
  "ART Label-Only Gap Accuracy (All)": radarResultsAccuracy,
  "ART Label-Only Gap Accuracy (OUT)": radarResultsAccuracy,
  "ART Label-Only Gap Accuracy (IN)": radarResultsAccuracy,
}

const buildRadarChartModel = ({
  chartData,
  displayOptions = {},
  legend = [],
}) => {
  const { dataPoints = null } = displayOptions
  const defaultReduced = Object.keys(DATA_POINT_KEYS).reduce(
    (all, k) => ({ ...all, [k]: { metric: k } }),
    {}
  )
  const chartMap = (pointKey, data) => {
    const value = DATA_POINT_KEYS[pointKey].value(data)
    return (
      dataMappers[pointKey] ? dataMappers[pointKey](value) : value
    ).toFixed(3)
  }

  const forAttackKeys = deleteRedundantAttackProps(chartData)
  const reduced = chartData.reduce((all, x, i) => {
    const attack = attackKey(forAttackKeys[i])
    return Object.keys(DATA_POINT_KEYS).reduce(
      (updated, k) => {
        if (legend[attack]?.checked) {
          updated[k][attack] = chartMap(k, x)
        }
        return updated
      },
      { ...all }
    )
  }, defaultReduced)
  const data = dataPoints.map((p) =>
    Object.values(reduced).find((x) => x.metric === p)
  )
  if (data && data.length) {
    const keys = Object.keys(data[0]).filter((x) => x !== "metric")
    return {
      data,
      keys,
      indexBy: "metric",
    }
  } else return { data: [], keys: [] }
}

const RadarChart = ({ chartData, displayOptions, legend }) => {
  const { data, keys, indexBy } = buildRadarChartModel({
    chartData,
    displayOptions,
    legend,
  })
  if (FEATURES.debug) console.log("radar-chart-data", data)
  return data ? (
    <ResponsiveRadar
      colors={({ key }) => legend[key]?.color || "rgba(0,0,0,0)"}
      data={data}
      keys={keys}
      indexBy={indexBy}
      maxValue="auto"
      dotBorderWidth={2}
      margin={{ top: 70, right: 150, bottom: 40, left: 145 }}
      gridLabelOffset={32}
      enableDots={true}
      dotSize={10}
      isInteractive={true}
      tooltipFormat={(value) => (FEATURES.debug ? value : "")}
    />
  ) : null
}

export default RadarChart
