import { PickContextProvider, PickContext } from "../../context/PickContext"
import { useContext, useState, useMemo } from "react"
import questions from "../../util/questions"
import styled from "styled-components"
import { Centered } from "../game/Util"

const OptionButton = styled.button`
  border: none;
  margin-right: ${({ inline }) => (inline ? "5px" : "0")};
  color: red;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  font-size: 1.1rem;
  padding: 5px;
  transition: background-color 0.1s linear;
  &:hover {
    text-decoration: underline;
    background-color: rgba(255, 255, 255, 0.3);
  }
`

const Option = ({ onClick, option, inline }) => (
  <OptionButton onClick={onClick} inline={inline}>
    {option.image ? <img src={option.image} alt="" /> : null}
    <div>{option.title}</div>
  </OptionButton>
)

const Options = ({ options = [], onClick, inline = false }) =>
  options.map((x) => (
    <Option
      key={x.title}
      onClick={() => onClick(x)}
      option={x}
      inline={inline}
    />
  ))

const Question = ({ configKey }) => {
  const [currentConfigKey, setCurrentConfigKey] = useState(configKey)
  const [configurations] = useContext(PickContext)
  const config = useMemo(() => questions[currentConfigKey], [currentConfigKey])
  const currentConfig =
    configurations.selections[configurations.selections.current]
  const inlineOptions = config.links?.inline || []
  const linksKey = currentConfig[config.linkKey] || "default"
  const options = config.links ? config?.links[linksKey] : []
  const contentStyle = {
    position: "absolute",
    padding: 25,
    top:
      !config.content || config.content?.location === "top"
        ? 0
        : config.content?.location === "center"
        ? "50%"
        : null,
    left: config.content?.location === "center" ? "50%" : null,
    transform:
      config.content?.location === "center"
        ? "translateY(-50%) translateX(-50%)"
        : null,
    bottom: config.content?.location === "bottom" ? 0 : null,
  }

  const outerStyle = {
    backgroundColor: "black",
    backgroundImage: config.image ? `url(${config.image})` : null,
    height: 600,
    width: 800,
    borderRadius: 15,
    position: "relative",
  }

  const onClickOption = (x) => setCurrentConfigKey(x.link)

  return (
    <Centered>
      <div style={outerStyle}>
        <div style={contentStyle}>
          <div style={{ color: "white" }}>
            {config.text}
            {inlineOptions && inlineOptions.length ? (
              <Options options={inlineOptions} onClick={onClickOption} />
            ) : null}
          </div>
          <div style={{ display: "flex", justifyContent: "space-evenly" }}>
            <Options options={options} onClick={onClickOption} inline />
          </div>
        </div>
      </div>
    </Centered>
  )
}

const Pick = () => (
  <PickContextProvider>
    <Question configKey="start" />
  </PickContextProvider>
)

export default Pick
