import { useState, useRef, useEffect, useCallback } from "react"
import styled from "styled-components"

import RiskScores from "./RiskScores"
import Blackjack from "./Blackjack"
import Pick from "../pick/Pick"

const ideas = {
  riskScores: {
    title: "Risk Scores",
    description: (
      <>
        <h3>Motivation</h3>
        <p>
          Various charts are great at visualizing complex multi-dimensional data
          and allowing for easy trade-off comparisons for design choices. Select
          graph type to get started.
        </p>
        <h3>Application to Membership Inference</h3>
        <p>
          Categories correspond to high-level training configurations such as
          Data set, Natural, DP SGD, Madry Robust, Label smoothing, number of
          epochs, batch size, etc. The dimensions correspond to specific MI
          Attacks types and metrics.
        </p>
        <h3>Way Ahead</h3>
        <p>
          Adding in interaction elements so that user can select which
          categories and dimensions they want displayed.
        </p>
      </>
    ),
    component: <RiskScores />,
  },
  blackjack: {
    title: "Blackjack",
    description: (
      <>
        <h3>Motivation</h3>
        <p>
          Desire a fun game with structured data that makes privacy attacks
          intuitive in how information is leaked through the model output.
        </p>
        <h3>Application to Membership Inference</h3>
        <p>
          In the attribute inference attack, the target model’s output is used
          to infer the value of a hidden or sensitive feature. Here, a model is
          trained to receive two cards and decide whether to hit or stay. The
          viz tool user is shown the model decisions (hit or stay) and one of
          the two cards and the model's decision. The user then guesses the
          value of the hidden card.
        </p>
        <h3>Way Ahead</h3>
        <p>
          Game needs to be fully implemented. Then, determine if the game
          achieves the goals of being easily understood while also accurately
          depicting the attack.
        </p>
      </>
    ),
    component: <Blackjack />,
  },
  pick: {
    title: "Pick Your Path",
    description: (
      <>
        <h3>Motivation</h3>
        <p>
          An interactive adventure where the user decides which story to follow
          as choices are presented. Story and related decisions are from the
          perspective of an NGA analyst.
        </p>
        <h3>Application to Membership Inference</h3>
        <p>
          In the training and deployment of deep neural network models, there
          are potential privacy and security concerns that can sometime compete
          with the goal of the most accurate model. Medium explores these
          tradeoffs in an engaging way.
        </p>
        <h3>Way Ahead</h3>
        <p>
          Team needs to finalize the use case scenarios, incorporate storyline
          elements and related scoring of tradeoffs.
        </p>
      </>
    ),
    component: <Pick />,
  },
}

const Container = styled.div`
  padding: 15px 0 0 15px;
`
const IdeaComponentContainer = styled.div`
  width: 98%;
  min-height: 600px;
  padding: 10px;
  border-top: 1px solid gray;
  position: relative;
`
const IdeaSelector = styled.div`
  display: inline-block;
  background-color: ${({ selected }) => (selected ? "slategray" : "white")};
  color: ${({ selected }) => (selected ? "white" : null)};
  font-size: 1.2rem;
  border-radius: 20px;
  border: 1px solid gray;
  margin: 0 5px 5px 0;
  padding: 5px 10px;
  &:hover {
    box-shadow: 0 0 4px gray;
    text-shadow: 1px 1px 1px gray;
  }
`
const IdeaButton = styled.button`
  background-color: rgba(0, 0, 0, 0);
  color: inherit;
  font-size: 1.2rem;
  border: none;
  cursor: pointer;
`
const IdeaDescriptionButton = styled.button`
  background-color: lightgray;
  border: 1px solid gray;
  width: 27px;
  font-size: 1rem;
  border-radius: 50%;
  cursor: pointer;
  &::after {
    content: "i";
  }
`
const DescriptionExpandCollapseButton = styled.button`
  position: absolute;
  right: 0;
  top: 0;
  width: 18px;
  height: 100%;
  border-radius: 0;
  background-color: lightgray;
  border: none;
  cursor: pointer;
  &::after {
    content: "❮";
  }
`
const StyledDescriptionSidePanel = styled.div`
  position: absolute;
  top: 10px;
  left: -15px;
  height: 90%;
  width: 350px;
  border-right: 1px solid lightgray;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  border-left: none;
  padding: 15px 25px 15px 15px;
  background-color: rgba(255, 255, 255, 0.95);
  transform: ${({ expanded }) =>
    expanded ? "translateX(0)" : "translateX(-100%)"};
  transition: transform 0.3s ease-out;
`

const DescriptionPanel = ({ panelRef, expanded, onClose, text }) => (
  <StyledDescriptionSidePanel expanded={expanded} ref={panelRef}>
    {expanded ? text : null}
    <DescriptionExpandCollapseButton
      expanded={expanded}
      onClick={() => onClose()}
    />
  </StyledDescriptionSidePanel>
)

const Ideas = () => {
  const [selectedIdea, setSelectedIdea] = useState("riskScores")
  const [selectedDescription, setSelectedDescription] = useState(null)
  const onClickIdea = (idea) => {
    setSelectedIdea(idea)
    if (selectedDescription) setSelectedDescription(idea)
  }
  const descriptionPanelRef = useRef(null)
  const onClickIdeaDescriptionButton = useCallback(
    (idea) => {
      setSelectedDescription(idea === selectedDescription ? null : idea)
      setSelectedIdea(idea)
    },
    [selectedDescription]
  )
  useEffect(() => {
    const handleClickOutside = (event) => {
      const ignoreTargets = () =>
        descriptionPanelRef.current?.contains(event.target) ||
        (event.target?.className?.includes &&
          (event.target.className.includes("idea-description-button") ||
            event.target.className.includes("idea-button")))

      if (!ignoreTargets()) {
        setSelectedDescription(null)
      }
    }

    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])
  return (
    <Container>
      <h1 style={{ marginTop: 0 }}>Ideas</h1>
      <div>
        {Object.keys(ideas).map((k) => (
          <IdeaSelector
            key={`idea-selector-${k}`}
            selected={k === selectedIdea}
          >
            <IdeaButton onClick={() => onClickIdea(k)} className="idea-button">
              {ideas[k].title}
            </IdeaButton>
            <IdeaDescriptionButton
              className="idea-description-button"
              idea={k}
              title={`Click for more information`}
              onClick={() => onClickIdeaDescriptionButton(k)}
            />
          </IdeaSelector>
        ))}
      </div>
      <IdeaComponentContainer>
        {ideas[selectedIdea].component}
        <DescriptionPanel
          panelRef={descriptionPanelRef}
          expanded={selectedDescription !== null}
          onClose={() => setSelectedDescription(null)}
          text={
            selectedDescription ? ideas[selectedDescription].description : null
          }
        />
      </IdeaComponentContainer>
    </Container>
  )
}

export default Ideas
