import React, { useEffect } from "react"
import { Field, useFormikContext } from "formik"
import styled from "styled-components"
import { Range, getTrackBackground } from "react-range"

export const FEATURES = {
  showCannedFilters: true,
  debug: false,
}

const InnerFilterLabel = styled.label`
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  &:last-child {
    padding-bottom: 2px;
  }
`

const TriState = ({ id }) => (
  <>
    <InnerFilterLabel>
      <Field type="checkbox" name={id} value="yes" />
      Yes
    </InnerFilterLabel>
    <InnerFilterLabel>
      <Field type="checkbox" name={id} value="no" />
      No
    </InnerFilterLabel>
  </>
)

const Options = ({ id, options }) =>
  options.map((o) => (
    <InnerFilterLabel key={`option-${id}-${o}`}>
      <Field type="checkbox" name={id} value={o} />
      {o}
    </InnerFilterLabel>
  ))

const RangeSelect = ({ id, min, max, step = 1, width = "100%" }) => {
  const RANGE_COLORS = ["#ccc", "slategray", "#ccc"]
  const { values, setFieldValue } = useFormikContext()
  useEffect(() => {
    if (!values[id]) {
      console.debug(`Defaulting range values for [${id}] to [${[min, max]}]...`)
      setFieldValue(id, [min, max])
    }
  })

  const rangeValues =
    values[id] && values[id].length === 2 ? values[id] : [min, max]

  return (
    <div
      style={{
        width,
        fontFamily: "Arial, Helvetica, sans-serif",
        fontSize: "12px",
      }}
    >
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        {min < max ? (
          <Range
            id={id}
            step={step}
            min={min}
            max={max}
            values={rangeValues || [0, 0]}
            onChange={(v) => setFieldValue(id, v)}
            renderTrack={({ props, children }) => (
              <div
                {...props}
                style={{
                  ...props.style,
                  height: "6px",
                  display: "flex",
                  margin: "10px 10% 2px 10%",
                  width: "80%",
                  background: getTrackBackground({
                    values: rangeValues,
                    colors: RANGE_COLORS,
                    min: min,
                    max: max,
                  }),
                  borderRadius: 3,
                }}
              >
                {children}
              </div>
            )}
            renderThumb={({ props }) => (
              <div
                {...props}
                style={{
                  ...props.style,
                  height: "12px",
                  width: "12px",
                  borderRadius: "50%",
                  backgroundColor: "#999",
                  outline: "none",
                }}
              />
            )}
          />
        ) : null}
        <div style={{ margin: "3px 0" }}>
          {rangeValues
            ? rangeValues[0] === rangeValues[1]
              ? rangeValues[0]
              : `${rangeValues[0]} - ${rangeValues[1]}`
            : "-"}
        </div>
      </div>
    </div>
  )
}

