export const deleteRedundantAttackProps = (data) => {
  if (FEATURES.debug || !data || data.length < 2) return data
  const inAll = (key) =>
    [...new Set(data.map((x) => String(x[key]).toLowerCase()))].length === 1
  let keysToRemove = Object.keys(ATTACK_KEY_PARTS)
    .filter((k) => !ATTACK_KEY_PARTS[k].alwaysShow)
    .reduce((all, k) => (inAll(k) ? [...all, k] : [...all]), [])

  return data.map((x) =>
    keysToRemove.reduce(
      (all, k) => {
        delete all[k]
        return all
      },
      { ...x }
    )
  )
}

const ATTACK_KEY_PARTS = {
  model_architecture: { alwaysShow: true },
  mode: {},
  epochs: { format: (x) => `${x}epochs` },
  batch_size: { format: (x) => `${x}batch` },
  optimizer_name: {},
  num_classes: { format: (x) => `${x}classes` },
  data_augmentation: { flag: "aug" },
  attack_type: {},
  dpsgd: { flag: "dpsgd" },
  label_smoothing: { flag: "smooth" },
  trainable: { flag: "trainable" },
}

export const attackKey = (x) => {
  const part = (key) => {
    if (x[key]) {
      if ("flag" in ATTACK_KEY_PARTS[key]) {
        return ATTACK_KEY_PARTS[key].flag
      } else {
        const formatFn = ATTACK_KEY_PARTS[key].format || ((x) => `${x}`)
        return formatFn(x[key])
      }
    } else return null
  }

  return Object.keys(ATTACK_KEY_PARTS)
    .map(part)
    .filter((x) => x)
    .map((x) => x.toLowerCase())
    .join("/")
}

