import {
  FEATURES,
  DATA_POINT_KEYS,
  deleteRedundantAttackProps,
  attackKey,
} from "./utils"
import { ResponsiveLine } from "@nivo/line"

const buildParallelCoordinateModel = ({
  chartData,
  displayOptions = {},
  legend = {},
}) => {
  const { dataPoints = null } = displayOptions
  const forAttackKeys = deleteRedundantAttackProps(chartData)
  const data = chartData.map((x, i) => {
    const _dataPoints = dataPoints || []
    const attack = attackKey(forAttackKeys[i])
    const chartMap = (pointKey, data) => {
      const value = DATA_POINT_KEYS[pointKey].value(data)
      return (
        DATA_POINT_KEYS[pointKey].chartMapper?.parallelCoordinates
          ? DATA_POINT_KEYS[pointKey].chartMapper.parallelCoordinates(value)
          : value
      ).toFixed(3)
    }
    const sorted = _dataPoints
      .map((k) => ({
        series: attack,
        x: k,
        y: chartMap(k, x),
      }))
      .filter((x) => legend[x.series]?.checked)
      .sort((a, b) =>
        _dataPoints.indexOf(a.x) > _dataPoints.indexOf(b.x) ? 1 : -1
      )

    return {
      id: attack,
      data: sorted,
    }
  })
  return { data }
}

const ParallelCoordinatesChart = ({ chartData, displayOptions, legend }) => {
  const { data } = buildParallelCoordinateModel({
    chartData,
    displayOptions,
    legend,
  })
  if (FEATURES.debug) console.log("parallel-coordinates-data", data)
  return data ? (
    <ResponsiveLine
      colors={({ id }) => legend[id]?.color || "rgba(0,0,0,0)"}
      data={data}
      margin={{ top: 50, right: 150, bottom: 90, left: 30 }}
      yScale={{
        type: "linear",
        min: "auto",
        max: "auto",
      }}
      useMesh={true}
      pointSize={10}
      pointBorderWidth={2}
      axisBottom={{ tickRotation: 25 }}
    />
  ) : null
}

export default ParallelCoordinatesChart
