import { deleteRedundantAttackProps, attackKey } from "./utils"
import styled from "styled-components"

const CheckboxLegendLabel = styled.label`
  font-size: 0.7rem;
  cursor: pointer;
  display: block;
  display: flex;
  align-items: center;
`

const CheckboxLegendCheck = ({ color, checked }) => {
  const width = 16
  const height = 16
  const strokeWidth = 2
  const checkColor = "white"
  const style = {
    stroke: color,
    fill: color,
    strokeWidth,
  }
  const checkPoints = [
    `${width * 0.3},${height * 0.5}`,
    `${width * 0.45},${height * 0.65}`,
    `${width * 0.7},${height * 0.32}`,
  ]
  return (
    <div style={{ width, height }}>
      <svg viewBox={`0 0 ${width} ${height}`}>
        <circle
          style={style}
          cx={width / 2}
          cy={height / 2}
          r={width / 2 - 2}
        />
        {checked ? (
          <polyline
            style={{
              ...style,
              stroke: checkColor,
              strokeWidth,
            }}
            points={checkPoints.join(" ")}
          />
        ) : null}
      </svg>
    </div>
  )
}

const CheckboxLegend = ({ legendModel, onLegendItemChecked }) =>
  legendModel ? (
    <div style={{ position: "relative", zIndex: 999 }}>
      {Object.keys(legendModel).map((key) => {
        return (
          <CheckboxLegendLabel
            key={`legend-key-${key}`}
            onClick={() =>
              onLegendItemChecked({
                key,
                checked: !legendModel[key].checked,
              })
            }
          >
            <CheckboxLegendCheck
              color={legendModel[key].color}
              checked={legendModel[key].checked}
            />
            <span>{key}</span>
          </CheckboxLegendLabel>
        )
      })}
    </div>
  ) : null

export const legendKeys = (data) => {
  const forAttackKeys = deleteRedundantAttackProps(data)
  return data.map((_, i) => attackKey(forAttackKeys[i]))
}

export default CheckboxLegend
