import { useState, useEffect, useMemo, useCallback } from "react"
import RadarChart from "./riskScores/Radar"
import {
  FEATURES,
  DEFAULT_FILTER,
  DEFAULT_DATA_POINTS,
  DATA_POINT_KEYS,
  COLORS,
} from "./riskScores/utils"
import ParallelCoordinatesChart from "./riskScores/ParallelCoordinates"
import CheckboxLegend, { legendKeys } from "./riskScores/Legend"
import {
  CannedFilterSelection,
  FilterForm,
  filtered,
  buildFilterOptions,
  updateFilterNoSelections,
} from "./riskScores/Filter"
import styled from "styled-components"
import Select from "react-select"

const StyledChartSelectionButton = styled.div`
  border: 1px solid gray;
  border-radius: 25px;
  background-color: ${({ checked }) =>
    checked ? "cornflowerblue" : "lightgray"};
  color: ${({ checked }) => (checked ? "black" : "white")};
  padding: 5px 10px 5px 0;
  margin-right: 10px;
  cursor: pointer;
  > * {
    cursor: inherit;
  }
`

const ChartSelectionButton = ({ id, label, checked, onSelect }) => (
  <StyledChartSelectionButton key={`chart-${id}`} checked={checked}>
    <input
      id={`chart-${id}`}
      type="radio"
      value={id}
      name="chart"
      checked={checked}
      onChange={onSelect}
    />
    <label htmlFor={`chart-${id}`}>{label}</label>
  </StyledChartSelectionButton>
)

const ChartSelection = ({ selectedChartId, setSelectedChartId }) => {
  const charts = {
    radar: "Radar Chart",
    parallelCoordinates: "Parallel Coordinates",
  }
  useEffect(() => {
    setSelectedChartId(selectedChartId) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  return (
    <div>
      <label>Chart</label>
      <div style={{ display: "flex" }}>
        {Object.keys(charts).map((x) => (
          <ChartSelectionButton
            key={`chart-selection-button-${x}`}
            id={x}
            label={charts[x]}
            checked={selectedChartId === x}
            onSelect={() => setSelectedChartId(x)}
          />
        ))}
      </div>
    </div>
  )
}

const DisplayOptions = ({ displayOptions, setDisplayOptions }) => {
  const options = Object.keys(DATA_POINT_KEYS).map((value) => ({
    value,
    label: value,
  }))

  const onChange = (value) =>
    setDisplayOptions({ dataPoints: (value || []).map((x) => x.value) })

  const values = () =>
    displayOptions.dataPoints?.map((value) => ({
      value,
      label: value,
    }))

  return (
    <div style={{ width: 725 }}>
      <label>Data Points</label>
      <Select
        isClearable={false}
        value={values()}
        isMulti
        placeholder="Select Data Points..."
        options={options}
        onChange={onChange}
        styles={{
          multiValueRemove: (base) =>
            displayOptions.dataPoints?.length <= 3 // Minimum 3 data points
              ? { ...base, display: "none" }
              : base,
        }}
      />
    </div>
  )
}

const RiskScores = () => {
  const [filter, setFilter] = useState(DEFAULT_FILTER)
  const [displayOptions, setDisplayOptions] = useState({
    dataPoints: DEFAULT_DATA_POINTS,
  })
  const [selectedChartId, setSelectedChartId] = useState("radar")
  const [rawRiskScores, setRawRiskScores] = useState([])
  const [filterOptions, setFilterOptions] = useState()
  const [chartData, setChartData] = useState(rawRiskScores)
  const [legend, setLegend] = useState({})
  const resetFilter = useCallback(() => {
    const _filterOptions = buildFilterOptions(rawRiskScores)
    setFilterOptions(_filterOptions)
    setFilter(updateFilterNoSelections(DEFAULT_FILTER, _filterOptions))
  }, [rawRiskScores])

  useEffect(() => {
    const getRawData = async () => {
      const result = await fetch("./data/risk-scores.json")
      const data = await result.json()
      setRawRiskScores(data)
    }
    getRawData()
  }, [])

  useEffect(() => {
    if (rawRiskScores && rawRiskScores.length) {
      resetFilter()
    }
  }, [rawRiskScores, resetFilter])

  useEffect(
    () => setChartData(filtered(rawRiskScores, filter)),
    [filter, rawRiskScores]
  )

  useEffect(() => {
    const model = legendKeys(chartData).reduce(
      (all, k, i) => ({
        ...all,
        [k]: { color: COLORS[i % COLORS.length], checked: true },
      }),
      {}
    )
    setLegend(model)
  }, [chartData])

  const charts = useMemo(
    () => ({
      radar: (
        <div
          style={{
            height: "100%",
            width: 830,
          }}
        >
          <RadarChart
            chartData={chartData}
            displayOptions={displayOptions}
            legend={legend}
          />
        </div>
      ),
      parallelCoordinates: (
        <div
          style={{
            height: "100%",
            width: 930,
          }}
        >
          <ParallelCoordinatesChart
            chartData={chartData}
            displayOptions={displayOptions}
            legend={legend}
          />
        </div>
      ),
    }),
    [chartData, displayOptions, legend]
  )

  return (
    <div style={{ display: "flex" }}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          marginLeft: -10,
          marginRight: 10,
        }}
      >
        {FEATURES.showCannedFilters ? (
          <CannedFilterSelection
            onSelectCannedFilter={(filter, dataPoints) => {
              setFilter(updateFilterNoSelections(filter, filterOptions))
              setDisplayOptions((prev) => ({ ...prev, dataPoints }))
            }}
          />
        ) : null}
        {filterOptions ? (
          <FilterForm
            filter={filter}
            filterOptions={filterOptions}
            setFilter={setFilter}
            onReset={resetFilter}
          />
        ) : null}
      </div>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <div style={{ display: "flex" }}>
          <ChartSelection
            selectedChartId={selectedChartId}
            setSelectedChartId={setSelectedChartId}
          />
          <DisplayOptions
            displayOptions={displayOptions}
            setDisplayOptions={setDisplayOptions}
          />
        </div>
        <div style={{ display: "flex", margin: 10 }}>
          <div
            style={{
              maxHeight: 650,
              minHeight: 350,
              height: 650,
              width: 800,
            }}
          >
            {selectedChartId ? charts[selectedChartId] : null}
          </div>
          <CheckboxLegend
            legendModel={legend}
            onLegendItemChecked={({ key, checked }) =>
              setLegend((prev) => ({
                ...prev,
                [key]: { ...prev[key], checked },
              }))
            }
          />
        </div>
      </div>
    </div>
  )
}

export default RiskScores
