import { useState, useMemo } from "react"
import styled from "styled-components"
import { randomItem } from "../util/misc"

const Colors = {
  lightsteelblue: "black",
  cornflowerblue: "white",
  khaki: "black",
  lightsalmon: "black",
}

const styleColors = (key) => ({
  backgroundColor: key,
  color: Colors[key],
})

const StyledText = styled.span`
  cursor: ${({ expanded }) => (expanded ? "w-resize" : "ew-resize")};
  border: ${({ expanded }) =>
    expanded ? "1px solid rgba(0,0,0,0)" : "1px solid gray"};
  border-radius: ${({ expanded }) => (expanded ? "1px solid gray" : "2px")};
  color: gray;
  background-color: lightgray;
  padding: 0 2px;
`

const StretchText = ({ text, style, children, nested = true }) => {
  const [expanded, setExpanded] = useState(false)
  const colorKey = useMemo(() => randomItem(Object.keys(Colors)), [])
  const _style = { ...styleColors(colorKey), ...style }
  return (
    <>
      {nested ? " " : null}
      <StyledText
        expanded={expanded}
        style={_style}
        onClick={() => setExpanded(!expanded)}
      >
        {expanded ? children : text}
      </StyledText>
      {nested ? " " : null}
    </>
  )
}

export default StretchText
