import { Centered, GameButton } from "./Util"
import styled from "styled-components"
import { Link } from "react-router-dom"

const Container = styled.div`
  border-radius: 10px;
  background-color: antiquewhite;
  box-shadow: 0 0 4px gray;
  text-align: center;
  padding: 40px;
`

const Title = styled.h1`
  font-size: 4rem;
`

const GameCards = styled.div`
  display: inline;
`

const GameCardContainer = styled.div`
  display: inline-block;
  border: 1px solid gray;
  background-color: lightslategray;
  color: white;
  text-shadow: 2px 2px 2px darkslategray;
  border-radius: 15px;
  padding: 10px;
  margin-right: 10px;
  box-shadow: 1px 1px 4px grey;
`

const GameCardTitle = styled.div`
  font-size: 1.6rem;
  margin: 15px;
`

const GameCardButton = styled(GameButton)`
  margin-right: 10px;
`

const ButtonContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-right: -10px;
`

const GameCard = ({ title, links }) => (
  <GameCardContainer>
    <GameCardTitle>{title}</GameCardTitle>
    <ButtonContainer>
      {links.map((x) => (
        <Link key={x.to} to={x.to}>
          <GameCardButton
            {...{ "data-tip": x.disabled ? "Coming soon" : null }}
          >
            {x.title || "Play"}
          </GameCardButton>
        </Link>
      ))}
    </ButtonContainer>
  </GameCardContainer>
)

const games = [
  {
    title: "20 Questions",
    links: [
      { title: "Learn", to: "/20q/learn" },
      { title: "Easy", to: "/20q/easy" },
      { title: "Hard", to: "/20q/hard" },
    ],
  },
  {
    title: "Match Game",
    links: [
      { title: "Learn", to: "/match/learn" },
      { title: "Easy", to: "/match/easy" },
      { title: "Hard", to: "/match/hard" },
    ],
  },
]

const Stack = styled.div`
  display: flex;
  flex-direction: column;
`
const OrScroll = styled.div`
  margin: 20px;
  text-align: center;
  &::after {
    font-size: 1.4rem;
    content: "or scroll down to learn the story behind the games...";
  }
`

const Menu = () => (
  <Centered>
    <Stack>
      <Container>
        <Title>Play A Game</Title>
        <GameCards>
          {games.map((x) => (
            <GameCard key={x.title} {...x} />
          ))}
        </GameCards>
      </Container>
      <OrScroll />
    </Stack>
  </Centered>
)

export default Menu
