import { useState } from "react"
import { DEFAULT_DATA_POINTS, DEFAULT_FILTER, FILTERS } from "./utils"
import { Formik, Form } from "formik"
import styled from "styled-components"
import Select from "react-select"
import { unique, isDef, isEmpty } from "../../../util/misc"

const StyledCannedFilterSelection = styled.div`
  font-size: 0.9rem;
`

const CANNED_FILTERS = {
  canned1: {
    label: "CIFAR10 Adam",
    filter: { dataSet: "cifar10", optimizerName: ["adam"] },
    dataPoints: ["Test Accuracy", "Train Time", "MPTL Accuracy (All)"],
  },
  canned2: {
    label: "EMNIST Natural",
    filter: { dataSet: "emnist", optimizerName: ["sgd"] },
    dataPoints: [
      "Train Accuracy",
      "Test Accuracy",
      "ART Blackbox Accuracy (All)",
      "ART Rule Based Accuracy (All)",
    ],
  },
}

export const CannedFilterSelection = ({ onSelectCannedFilter }) => {
  const custom = { value: "custom", label: "Custom Filter..." }
  const [selection, setSelection] = useState(custom)
  return (
    <StyledCannedFilterSelection>
      <Select
        placeholder="Select Filter..."
        options={[
          custom,
          ...Object.keys(CANNED_FILTERS).map((value) => ({
            value,
            ...CANNED_FILTERS[value],
          })),
        ]}
        onChange={(selected) => {
          setSelection(selected)
          const cannedFilter = CANNED_FILTERS[selected.value]
          onSelectCannedFilter(
            cannedFilter?.filter || DEFAULT_FILTER,
            cannedFilter?.dataPoints || DEFAULT_DATA_POINTS
          )
        }}
        styles={{
          control: (base) => ({
            ...base,
            border: 0,
            boxShadow: "none",
          }),
          singleValue: (base) => ({
            ...base,
            color: "black",
          }),
        }}
        value={selection}
      />
    </StyledCannedFilterSelection>
  )
}

const FilterContainer = styled.div`
  display: flex;
  background-color: white;
  border: 1px solid gray;
`

const FilterLabel = styled.label`
  background-color: slategray;
  color: white;
  padding: 5px 8px;
  &:before {
    content: "❯ ";
  }
`

const Labelled = ({ htmlFor, children }) => (
  <div
    style={{
      display: "flex",
      flexDirection: "column",
      width: "100%",
    }}
  >
    <FilterLabel htmlFor={htmlFor}>
      {FILTERS.find((x) => x.id === htmlFor).label}
    </FilterLabel>
    {children}
  </div>
)

const FormSubmitButton = styled.button`
  border: 1px solid gray;
  color: white;
  background-color: darkslategray;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 5px;
  &:hover {
    box-shadow: 0 0 1px gray;
    text-shadow: 1px 1px 1px gray;
  }
`

export const FilterForm = ({ filter, setFilter, filterOptions, onReset }) => {
  const filterNullsToBlanks = () => {
    const toBlanks = { ...filter }
    "dataAugmentation modelArchitecture mode optimizerName dpsgd labelSmoothing"
      .split(" ")
      .forEach((x) => {
        if (!isDef(toBlanks[x])) toBlanks[x] = ""
      })
    return toBlanks
  }
  const blankedFilter = filterNullsToBlanks()
  return (
    <FilterContainer>
      <Formik
        initialValues={blankedFilter}
        enableReinitialize
        onSubmit={async (values) => {
          const mapped = FILTERS.filter((x) => "mapValue" in x).reduce(
            (all, x) => ({ ...all, [x.id]: x.mapValue(all[x.id]) }),
            values
          )
          setFilter(mapped)
        }}
      >
        <Form
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "end",
          }}
        >
          {FILTERS.map((f) => (
            <Labelled key={`filter-property-${f.id}`} htmlFor={f.id}>
              {f.control(filterOptions)}
            </Labelled>
          ))}
          <div
            style={{
              display: "flex",
              justifyContent: "space-evenly",
              width: "100%",
              margin: "10px 0",
            }}
          >
            <FormSubmitButton type="submit">❯ Filter</FormSubmitButton>
            <FormSubmitButton type="reset" onClick={onReset}>
              ⟲ Reset
            </FormSubmitButton>
          </div>
        </Form>
      </Formik>
    </FilterContainer>
  )
}

const inRange = (value, range) => {
  if (Array.isArray(range)) {
    if (range.length !== 2) return false
    const [min, max] = range
    return isDef(min) && isDef(max)
      ? value >= min && value <= max
      : isDef(min)
      ? value >= min
      : isDef(max)
      ? value <= max
      : false
  } else return value === range
}

export const filtered = (data, filterProps) => {
  const filter = (x) =>
    Object.keys(filterProps).reduce((all, k) => {
      const _filter = FILTERS.find((fk) => fk.id === k)
      if (!_filter) return all && true
      let value = isDef(x[_filter.sourceKey])
        ? x[_filter.sourceKey]
        : _filter.defaultValue

      switch (_filter.type) {
        case "array":
          value = isDef(value) ? value : []
          const selected = (
            Array.isArray(filterProps[k]) ? filterProps[k] : [filterProps[k]]
          ).map((x) => x.toLowerCase())
          return isEmpty(selected)
            ? all && true
            : all && selected.includes(value.toLowerCase())
        case "boolean":
          value = isDef(value) ? value : false
          return isEmpty(filterProps[k])
            ? all && true
            : all &&
                filterProps[k]
                  .map((x) => (x === "yes" ? true : false))
                  .includes(value)
        case "range":
          return isDef(filterProps[k])
            ? all && inRange(value, filterProps[k])
            : all && true
        default:
          return all && true
      }
    }, true)

  return data.filter(filter)
}

export const buildFilterOptions = (data, configs) => {
  const defaults = {
    array: () => [],
    range: () => ({
      min: 99999999,
      max: 0,
    }),
  }
  const filterOptions = []
  const updateFns = {
    array: (x, sourceKey, targetKey) =>
      (filterOptions[targetKey] = unique([
        ...filterOptions[targetKey],
        x[sourceKey].toLowerCase(),
      ])),
    range: (x, sourceKey, targetKey) =>
      (filterOptions[targetKey] = {
        min: Math.min(filterOptions[targetKey].min, x[sourceKey]),
        max: Math.max(filterOptions[targetKey].max, x[sourceKey]),
      }),
  }

  configs =
    configs || FILTERS.filter((x) => ["array", "range"].includes(x.type))
  data.forEach((x) => {
    configs.forEach(({ type, sourceKey, id }) => {
      if (!(id in filterOptions)) {
        filterOptions[id] = defaults[type]()
      }

      updateFns[type](x, sourceKey, id)
    })
  })

  return filterOptions
}

export const updateFilterNoSelections = (filter, filterOptions) =>
  FILTERS.reduce((all, x) => {
    switch (x.type) {
      case "array":
        return isEmpty(filter[x.id])
          ? { ...all, [x.id]: filterOptions[x.id] }
          : { ...all }
      case "boolean":
        return isEmpty(filter[x.id])
          ? { ...all, [x.id]: ["yes", "no"] }
          : { ...all }
      default:
        return { ...all }
    }
  }, filter)
