import { GameContextProvider } from "../context/GameContext"
import { useState } from "react"
import { withRouter } from "react-router-dom"
import styled from "styled-components"
import { Fab, Action } from "react-tiny-fab"
import "react-tiny-fab/dist/styles.css"
import TwentyQuestions from "../components/game/TwentyQuestions"
import Match from "../components/game/Match"
import TwentyQuestionsPractice from "../components/game/TwentyQuestionsPractice"
import Menu from "../components/game/Menu"
import Story from "../components/Story"
import Pick from "../components/pick/Pick"
import Ideas from "../components/ideas/Ideas"
import { Emoji } from "../components/game/Util"
import { isDef } from "../util/misc"
import Modal from "react-modal"
import { HashRouter as Router, Switch, Route } from "react-router-dom"

Modal.setAppElement("#root")

const CloseModal = styled.button`
  position: absolute;
  top: 5px;
  right: 0;
  border: 0;
  background: none;
  cursor: pointer;
`

const EmailIcon = ({
  width = 15,
  height = width * 0.7,
  color = "white",
  strokeWidth = 2,
}) => (
  <svg width={width} height={height}>
    <g>
      <rect
        fill="rgba(0,0,0,0)"
        height={height}
        width={width}
        stroke={color}
        strokeWidth={strokeWidth}
        x="0"
        y="0"
      />
      <line
        stroke={color}
        strokeWidth={strokeWidth / 2}
        x1="0"
        x2={width / 2}
        y1="0"
        y2={height * 0.6}
      />
      <line
        stroke={color}
        strokeWidth={strokeWidth / 2}
        x1={width}
        x2={width / 2}
        y1="0"
        y2={height * 0.6}
      />
    </g>
  </svg>
)

const CloseButton = ({
  width = 15,
  height = width,
  color = "black",
  strokeWidth = 2,
}) => (
  <svg width={width} height={height}>
    <g>
      <circle
        fill={color}
        height={height}
        width={width}
        stroke={color}
        cx={width / 2}
        cy={height / 2}
        r={width / 2}
      />
      <line
        stroke="white"
        strokeWidth={strokeWidth}
        x1={width / 4}
        x2={(width / 4) * 3}
        y1={height / 4}
        y2={(height / 4) * 3}
      />
      <line
        stroke="white"
        strokeWidth={strokeWidth}
        x1={(width / 4) * 3}
        x2={width / 4}
        y1={height / 4}
        y2={(height / 4) * 3}
      />
    </g>
  </svg>
)

const MessageUs = () => {
  const linkStyle = {
    color: "white",
    fontWeight: "bold",
  }
  return (
    <div
      style={{
        position: "absolute",
        bottom: 10,
        color: "white",
        width: "95%",
      }}
    >
      <h2>Message Us</h2>
      <p>
        We hope more people are interested in these kinds of problems. Please
        message us if you have an operational use case.
      </p>
      <hr />
      <p>
        Daniel Felps | <EmailIcon />{" "}
        <a href="mailto:daniel.l.felps@nga.mil" style={linkStyle}>
          daniel.l.felps@nga.mil
        </a>{" "}
        | <EmailIcon />{" "}
        <a href="mailto:daniel.l.felps@coe.ic.gov" style={linkStyle}>
          daniel.l.felps@coe.ic.gov
        </a>
      </p>
    </div>
  )
}

const AboutUs = () => {
  return (
    <div
      style={{
        padding: 10,
        backgroundColor: "rgba(100,100,100,0.6)",
        borderRadius: 4,
        border: "1px solid black",
        position: "absolute",
        bottom: 10,
        right: 10,
        color: "white",
        width: "50%",
      }}
    >
      <h2 style={{ marginTop: 0 }}>Project Guru</h2>
      <p>What can an adversary learn about our data through our ML models?</p>
      <p>
        Understand how model design choices make it easier or harder to attack.
      </p>
      <p>
        Deliver models that have similar utility but mitigate vulnerabilities.
      </p>
    </div>
  )
}

const GameModal = withRouter(({ history }) => {
  const ModalContent = {
    messageUs: {
      modalContentStyle: {
        backgroundImage: "url(img/mullet.png)",
      },
      content: <MessageUs />,
    },
    aboutUs: {
      modalContentStyle: {
        backgroundImage: "url(img/playground.png)",
      },
      content: <AboutUs />,
    },
  }
  const [modalContent, setModalContent] = useState()
  const returnToMenu = () => {
    if (window.location.hash === "#/") {
      // Already on the main page, scroll up to the menu
      window.scrollTo({ top: 0, left: 0, behavior: "auto" })
    } else {
      history.push("/")
    }
  }

  const dimensions = {
    height: 510,
    width: 900,
  }

  return (
    <>
      <Modal
        isOpen={isDef(modalContent)}
        onRequestClose={() => setModalContent(null)}
        style={{
          overlay: {
            backgroundColor: "#ededed",
          },
          content: {
            position: "fixed",
            top: "50%",
            left: "50%",
            bottom: null,
            right: null,
            transform: "translate(-50%, -50%)",
            height: dimensions.height * 0.75,
            width: dimensions.width * 0.75,
            minHeight: dimensions.height * 0.75,
            minWidth: dimensions.width * 0.75,
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center center",
            ...ModalContent[modalContent]?.modalContentStyle,
          },
        }}
      >
        {ModalContent[modalContent]?.content}
        <CloseModal onClick={() => setModalContent(null)}>
          <CloseButton color="black" isSelected={true} />
        </CloseModal>
      </Modal>
      <Fab
        alwaysShowTitle={true}
        mainButtonStyles={{ backgroundColor: "slategray" }}
        icon=". . ."
      >
        <Action text="Back to Game Menu" onClick={returnToMenu}>
          <Emoji symbol="0x21A9" />
        </Action>
        <Action text="Contact" onClick={() => setModalContent("messageUs")}>
          <EmailIcon />
        </Action>
        <Action text="Ideas" onClick={() => history.push("/ideas")}>
          ?
        </Action>
        <Action text="About" onClick={() => setModalContent("aboutUs")}>
          i
        </Action>
      </Fab>
    </>
  )
})

const GameView = () => {
  return (
    <GameContextProvider>
      <Router>
        <Switch>
          <Route path="/20q/learn">
            <TwentyQuestionsPractice />
          </Route>
          <Route path="/20q/easy">
            <TwentyQuestions gameMode="easy" />
          </Route>
          <Route path="/20q/hard">
            <TwentyQuestions gameMode="hard" />
          </Route>
          <Route path="/match/learn">
            <Match gameMode="learn" />
          </Route>
          <Route path="/match/easy">
            <Match gameMode="easy" />
          </Route>
          <Route path="/match/hard">
            <Match gameMode="hard" />
          </Route>
          <Route path="/pick">
            <Pick />
          </Route>
          <Route path="/ideas">
            <Ideas />
          </Route>
          <Route path="/">
            <Menu />
            <Story />
          </Route>
        </Switch>
        <GameModal />
      </Router>
    </GameContextProvider>
  )
}

export default GameView
